import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import loginSide from "../../assets/images/ins-img.png"; // Image for side section
import logo from "../../assets/images/logo.svg"; // Logo image
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../context/AuthContext";

const LearnerProfile = () => {
  const navigate = useNavigate();
   const {  setRole } = useAuth();

  // Get user data from localStorage
  const userDetails = JSON.parse(sessionStorage.getItem("userDetails"));

  const handleLogout = () => {
    // Clear the localStorage
    sessionStorage.removeItem("authToken");
    sessionStorage.removeItem("userDetails");
    sessionStorage.removeItem('role')
    setRole('')

    // Redirect to login page
    navigate("/learner-login");
  };

  if (!userDetails) {
    navigate("/learner-login"); // Redirect to login if no user data found
    return null;
  }
  return (
    <Container>
      <Row>
        <Col lg={12}>
          <div className="LoginFormWrp d-flex">
            <div className="LoginFormWrp__left">
              <img src={loginSide} alt="Login Side" />
              <div className="LoginFormWrp__logo">
                <img src={logo} alt="logo" />
              </div>
            </div>
            <div className="LoginFormWrp__right">
              <div>
                <h2>
                  Welcome, {userDetails.first_name} {userDetails.last_name} !
                </h2>
                <br/>
                <p>Email: {userDetails.email}</p>
                {/* <p>Contact No: {userDetails.contact_no}</p> */}
                <button
                  className="btn btn-white has-hover-blue mt-3"
                  onClick={handleLogout}
                >
                  Logout
                </button>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default LearnerProfile;
