import React, { useState, useEffect } from 'react';
import PlacesAutocomplete, { geocodeByAddress } from 'react-places-autocomplete';

const AutocompleteAddress = ({ handleSetLocalData = null, setAddressSuburb =null, billingAddrs=null, generalBillAdders=null, setLearnerBookData=null }) => {
  //handleSetLocalData is using in general website at the time of booking, book now api
  //second parameter setAddressSuburb, billingAddrs is using in trainer dashboard, banking details
  //generalBillAdders is using in general website for bill address autocomplete, pay now api
  //setLearnerBookData is using in learner dashboard at the pay now api, pay for booking
  const [address, setAddress] = useState('');
  const [mapsLoaded, setMapsLoaded] = useState(false);
  const [loadingError, setLoadingError] = useState(false);

  const googleMapsApiKey = process.env.REACT_APP_GOOGLE_MAP_KEY;

  useEffect(() => {
    const loadGoogleMapsScript = () => {
      // Check if a Google Maps script is already loaded
      const existingScript = document.querySelector(
        'script[src^="https://maps.googleapis.com/maps/"]'
      );

      // Remove the existing script if it doesn't have the "places" library
      if (existingScript && !existingScript.src.includes('libraries=places')) {
        document.head.removeChild(existingScript);
      }

      // Check again if the script with "places" is already loaded
      const scriptWithPlaces = document.querySelector(
        `script[src="https://maps.googleapis.com/maps/api/js?key=${googleMapsApiKey}&loading=async&libraries=places&callback=initMap"]`
      );

      if (scriptWithPlaces) {
        // If the correct script exists, wait for it to load
        scriptWithPlaces.onload = () => setMapsLoaded(true);
        scriptWithPlaces.onerror = () => setLoadingError(true);
        return;
      }

      // Create and load a new script with "places"
      const script = document.createElement('script');
      script.src = `https://maps.googleapis.com/maps/api/js?key=${googleMapsApiKey}&loading=async&libraries=places&callback=initMap`;
      script.async = true;
      //script.defer = true;

      script.onload = () => {
        setMapsLoaded(true);
        setLoadingError(false);
      };

      script.onerror = () => {
        console.error('Error loading Google Maps script.');
        setLoadingError(true);
      };

      document.head.appendChild(script);

      // Clean up the script on unmount
      return () => {
        if (script) {
          document.head.removeChild(script);
        }
      };
    };

    loadGoogleMapsScript();
  }, [googleMapsApiKey]);

useEffect(()=>{
  if(billingAddrs){ // this is using to autofill the input from the response in booking detail page get request
    setAddress(billingAddrs)
   // console.log(billingAddrs);
  }
},[billingAddrs])

  const handleSelect = async (selectedAddress) => {
    setAddress(selectedAddress);
    try {
      const results = await geocodeByAddress(selectedAddress);
     // console.log(results)
      const formattedAddress = results[0].formatted_address;
      console.log('Selected Address:', formattedAddress);
      console.log(results[0].address_components, results[0].address_components[results[0].address_components.length - 1].long_name) //this is array
      const addressComponents =results[0].address_components;
////-------------------------------------------------------------------- Function to find short_name based on required types
const getShortNameByTypes = (data, requiredTypes) => {
  const result = data.find(item =>  requiredTypes.every(type => item.types.includes(type)) );
  return result ? result.short_name : null;
};

const getLongNameByTypes = (data, requiredTypes) => {
  const result = data.find(item =>  requiredTypes.every(type => item.types.includes(type)) );
  return result ? result.long_name : null;
};

const postalCode = (data, requiredTypes) => {
  const result = data.find(item =>  requiredTypes.every(type => item.types.includes(type)) );
  return result ? result.short_name : results[0].address_components[results[0].address_components.length - 1].long_name;
};

// Required types to search for
const state = ["administrative_area_level_1", "political"];
const suburb = [ "locality" , "political" ];
const postcode = ["postal_code"];
// Get the short_name
const stateshortName = getShortNameByTypes(addressComponents, state);
const statelongName=getLongNameByTypes(addressComponents, state);

const suburbShortName = getShortNameByTypes(addressComponents, suburb);
const suburbLongName=getLongNameByTypes(addressComponents, suburb);

const addressPostCode =postalCode(addressComponents, postcode);

const generalBillStateShortName = getShortNameByTypes(addressComponents, state);
const generalBillSuburbShortName =  getShortNameByTypes(addressComponents, suburb)
//--------------------------------------------------------------------------------
//---------------------------------------------------------------

      if (handleSetLocalData) {
        console.log(stateshortName, '-', suburbLongName, '-',statelongName,'-', addressPostCode)
        handleSetLocalData((prev) => ({
          ...prev,
          street: formattedAddress,
          state_slug:stateshortName,
          suburb:suburbLongName,
          state_name:statelongName,// new requirement for address in autocomplete
          postcode :addressPostCode,// new requirement for address in autocomplete
        }));
      }// handleSetLocalData is using for general booking in book now and getting user address, not for billing address
      if (setAddressSuburb) {
        setAddressSuburb(results[0].address_components);
      }
      if(generalBillAdders){
        // handleSetLocalData((prev) => ({
        //   ...prev,
        //   billing_address: formattedAddress,
        //   generalBillState:generalBillStateShortName,
        //   generalBillSuburb:generalBillSuburbShortName
        // }));
        generalBillAdders((prev) => ({
          ...prev,
          billing_address: formattedAddress,
          generalBillState:generalBillStateShortName,
          generalBillSuburb:generalBillSuburbShortName,
          billing_suburb :suburbLongName,
          billing_state_slug :stateshortName,
          billing_state_name:statelongName,
          billing_postcode:Number(addressPostCode),
        }));
      }
      if(setLearnerBookData){ // this is for learner dashboard book now setting the billing data
        setLearnerBookData((prev) => ({
          ...prev,
          billing_address: formattedAddress,
          billing_suburb :suburbLongName,
          billing_state_slug :stateshortName,
          billing_state_name:statelongName,
          billing_postcode:Number(addressPostCode),
        }));
      }
// --------------------------------------------------------------

    } catch (error) {
      console.error('Error geocoding address:', error);
      alert('Error finding the address');
    }
  };

  const handleInputChange = (value) => {
    setAddress(value);
  };

  const handleError = (status, clearSuggestions) => {
    console.error('Error from Google Maps API', status);
    clearSuggestions();
  };

  const searchOptions = {
    types: ['address'],
    componentRestrictions: { country: 'au' },
  };

  if (loadingError) {
    return (
      <div style={{ textAlign: 'center', padding: '20px' }}>
        <h4>Error Loading Google Maps</h4>
        <p>
          Please check your API key and network connection. Try refreshing the
          page or contact support.
        </p>
      </div>
    );
  }



  return (
    <PlacesAutocomplete
      value={address}
      onChange={handleInputChange}
      onSelect={handleSelect}
      onError={handleError}
      searchOptions={searchOptions}
    >
      {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
        <div style={{ position: 'relative' }}>
          <input
            style={{ width: '100%' }}
            {...getInputProps({
              placeholder: 'Enter Address',
              className: 'location-search-input form-control',
            })}
          />
          <ul className="autocomplete-dropdown-container">
            {loading && <div>Loading suggestions...</div>}
            {suggestions.map((suggestion) => (
              <li
                {...getSuggestionItemProps(suggestion, {
                  className: suggestion.active
                    ? 'suggestion-item--active'
                    : 'suggestion-item',
                  style: suggestion.active
                    ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                    : { backgroundColor: '#ffffff', cursor: 'pointer' },
                })}
                key={suggestion.place_id} data-div={suggestion.description}
              >
                <span>{suggestion.description}</span>
              </li>
            ))}
          </ul>
        </div>
      )}
    </PlacesAutocomplete>
  );
};

export default AutocompleteAddress;
