import React, { useState, useEffect } from "react";
import { apiPost } from "../../utilities/axiosInstance";
import API_ENDPOINTS from "../../utilities/apiEndPoints";
import { useFormik } from "formik";
import * as Yup from "yup"; // For validation
import Payment from "../Payment/Payment.jsx";
import AutocompleteAddress from '../AutocompleteAddress';
import toDDMMYYYY from '../NewFormatDate';

function Step4({
  handleLocalData,
  handleSetLocalData,
  handleDrivingLesson,
  handleDrivingLessonCost,
  handleTestPackage,
  handlePackageSum,
  //sendTestPackageCost,
  testPackages,
  handleStatesData,
  inputValue, //for using the id for suburb and passing to payment api
  trainerID,
  handleGetHours,
  lessonBookingData,
  suburbPostCode
}) {

  const [searchValue, setSearchValue]=useState("")
  const [suggestions, setSuggestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [noResults, setNoResults] = useState(false);
 // const [isValidInput, setIsValidInput] = useState(true);
  const [isSuggestionSelected, setIsSuggestionSelected] = useState(false);
  //const [postCode, setPostCode] = useState('')
  /**search suburb */
  useEffect(() => {
    if (!isSuggestionSelected && searchValue.trim() !== "") {
      const delayDebounceFn = setTimeout(() => {
        fetchSuggestions(searchValue);
      }, 500);

      return () => clearTimeout(delayDebounceFn);
    }
  }, [searchValue, isSuggestionSelected]);

  //const validateInput = (value) => /^[a-zA-Z0-9\s]*$/.test(value);

  const handleInputChange = (e) => {
    const value = e.target.value;
    // console.log(validateInput(value), value)
      setSearchValue(value);
     // setIsValidInput(true);
      setIsSuggestionSelected(false);
  };

  const fetchSuggestions = async (query) => {
    setLoading(true);
    setNoResults(false);

    try {
      const response = await apiPost(API_ENDPOINTS.FIND_SUBURBS, {
        filter: query,
      });

      if (response.success && response.data) {
        const filteredSuggestions = response.data.filter((item) =>
          [item.suburb, item.stateCode, item.postcode.toString()].some((field) =>
            field.toLowerCase().includes(query.toLowerCase())
          )
        );

        setNoResults(filteredSuggestions.length === 0);
        setSuggestions(filteredSuggestions);
      } else {
        setSuggestions([]);
        setNoResults(true);
      }
    } catch (error) {
      console.error("Error fetching suggestions:", error);
      setSuggestions([]);
      setNoResults(true);
    } finally {
      setLoading(false);
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setSearchValue(`${suggestion.suburb}, ${suggestion.stateCode} (${suggestion.postcode})`);
   // setInputValue(suggestion.postcode);
  //  setPostCode(suggestion.postcode)// for adding the post code on select event and send to api
    setIsSuggestionSelected(true);
    setSuggestions([]);
    handleSetLocalData((prev) => ({
      ...prev,
      billing_suburb_id: suggestion.id,
    }))
   // console.log(suggestion.id)
  };
  /**search subburb */
  




//-----------------------------------------------------------
  useEffect(()=>{
   // console.log(lessonBookingData)
   
  },[inputValue,lessonBookingData])
  function calculateTotalDuration(lessonBookingData) {  //this function is to calculate the total hours selected
    return lessonBookingData.reduce((total, lesson) => total + +lesson.lesson_hours, 0);
  }
  let totalDuration = calculateTotalDuration(lessonBookingData);

  const dateString = lessonBookingData[0]?.lesson_date;
  toDDMMYYYY(dateString)
  // Manually format the date
  const formattedDate = toDDMMYYYY(dateString);
 // console.log(formattedDate); // Outputs: 24 Jan 2024

  //------------------------------

  const formik = useFormik({
    enableReinitialize: true, // ✅ Allow form to reinitialize when values change
    initialValues: {
      // billing_address: handleLocalData.billing_address,
      // billing_suburb_id: handleLocalData.billing_suburb_id,
      // billing_state_id:  handleLocalData.billing_state_id,
      billing_name:      handleLocalData.billing_name,
      billing_address: handleLocalData.billing_address,
     // billing_suburb_id: handleLocalData.generalBillSuburb,
     // billing_state_id:  handleLocalData.generalBillState,
  
    },
    validationSchema: Yup.object({
      billing_address: Yup.string().required("Billing Address is required"),
      //billing_suburb_id: Yup.string().required("Suburb is required"),
      //billing_state_id:Yup.string().required("State is required"),
      billing_name:Yup.string().required("Billing Name is required"),
    }),
    onSubmit: async (e) => {
      //e.preventDefault();
   //   console.log('true', e)
    
      // Prepare the payload for the API call
     
        // Additional logic here if required (e.g., handling Stripe payment)
      
  
    },
  });





  return (
    <div className="bookNow__stepWrap d-flex">
      <div className="bookNow__stepWrapLeft">
        <h2>Payment Details</h2>

        <div className="RegistrationForm">
          <h3>Billing Details</h3>
          <div className="RegistrationForm__group">
            <div className="RegistrationForm__field">
              <label htmlFor="*Billing Name">*Billing Name</label>
              <input type="text" name="billing_name" id="billing_name" value={handleLocalData.billing_name} required
                onChange={(e) => handleSetLocalData((prev) => ({ ...prev, billing_name: e.target.value, }))  } placeholder="Enter Billing name here.."
              />
              {formik.touched.billing_name && formik.errors.billing_name && (<div style={{ color: "red",marginTop:'0px' }}>{formik.errors.billing_name}</div>)}
            </div>
            <div className="RegistrationForm__field">
              <label htmlFor="*Billing Address">*Billing Address</label>
              <AutocompleteAddress generalBillAdders={handleSetLocalData}/>
              {/* <input type="text" name="billing_address" id="billing_address" value={handleLocalData.billing_address} required
                onChange={(e) => handleSetLocalData((prev) => ({...prev, billing_address: e.target.value,}))
                }
              /> */}
              {formik.touched.billing_address && formik.errors.billing_address && (<div style={{ color: "red",marginTop:'0px' }}>{formik.errors.billing_address}</div>)}
            </div>
          </div>
          <div className="RegistrationForm__group">
            <div className="RegistrationForm__field" style={{'position':'relative'}}>
              <label htmlFor="*Suburb">*Suburb</label>
              <input type="text" placeholder="Enter your Suburb here..." id="suburb" name="suburb" readOnly value={handleLocalData.billing_suburb}
                   disabled={true} />
              {/* <input type="text" name="billing_suburb_id" id="billing_suburb_id" value={searchValue} required onChange={handleInputChange} />
              <ul className="suggestions-list">
              {loading && <li>Loading...</li>}
              {noResults && !loading && (
              <li>No results found for "{searchValue}"</li>
              )}

              {suggestions.length > 0 && !loading && (
              suggestions.map((suggestion, index) => (
                <li key={index} onClick={() => handleSuggestionClick(suggestion)} style={{ cursor: "pointer" }} >
                  <div> <strong>{suggestion.suburb}</strong>, {suggestion.stateCode}{" "} ({suggestion.postcode}) </div>
                </li>
              ))
              )}
              </ul> */}
              {formik.touched.billing_suburb_id && formik.errors.billing_suburb_id && (<div style={{ color: "red",marginTop:'0px' }}>{formik.errors.billing_suburb_id}</div>)}
            </div>
            <div className="RegistrationForm__field">
              <label htmlFor="*State">*State</label>
              <input type="text" placeholder="Enter your State here..." id="state_id" name="state_id" readOnly value={handleLocalData.billing_state_name}
                   disabled={true} />
              {/* <select name="billing_state_id" id="billing_state_id" value={handleLocalData.billing_state_id} required
                onChange={(e) => handleSetLocalData((prev) => ({...prev, billing_state_id: e.target.value, })) }
              >
                <option value="">Please Select</option>
                {handleStatesData?.map((state) => ( <option key={state.id} value={state.id}> {state.name} </option> ))}
              </select> */}
              {formik.touched.billing_state_id && formik.errors.billing_state_id && (<div style={{ color: "red",marginTop:'0px' }}>{formik.errors.billing_state_id}</div>)}
            </div>
          </div>
          <hr />
         
          <Payment
            PayHandlePackageSum={handlePackageSum}
            userInfo={handleLocalData}
            handeLessonHours={handleGetHours}
            handleTestPackageStatus={handleTestPackage}
            handleTestPackageId={testPackages[0]?.id}
            suburb_id={inputValue}
            instructorID={trainerID}
            suburbPostCode={suburbPostCode}
            lessonBookingData={lessonBookingData}
            handleDrivingLessonCost={handleDrivingLessonCost}
            handleDrivingLesson={handleDrivingLesson}
            testPackages={testPackages}
            formikSubmit={formik.handleSubmit}
          />
          
          <div className="RegistrationForm__group">
            <div className="RegistrationForm__field">
              {/* <button type="submit" className="btn btn-blue w-100">
            CONFIRM: MAKE PAYMENT
            </button> */}
            </div>
          </div>
        </div>
      </div>
      {/* Right Side: Selected Package Summary */}
      <div className="bookNow__stepWrapRight">
        <div className="bookNow__drivingLessons">
          <div className="bookNow__drivingLessonsHead text-center text-black bg-light-grey">
            <h4>Your Selected Package</h4>
          </div>
          <div className="bookNow__subTotal bg-white">
            <div className="bookNow__subTotalrow d-flex justify-content-between">
              <p>You are about to make the following bookings:</p>
            </div>
      
              <div className="bookNow__subTotalrow d-flex justify-content-between align-items-center">
              <strong className="d-flex">{handleGetHours} x Driving Lesson{handleGetHours > 1 ? 's':''}
              <br />
              {lessonBookingData.length > 0 ? `${totalDuration} Hours Booked`:''}

              </strong>
              <p> {lessonBookingData.length > 0 ? formattedDate :''}, {lessonBookingData.length > 0 ? lessonBookingData[0].lesson_time :''}</p> 
              </div>

              {handleTestPackage ? (
              <div className="bookNow__subTotalrow d-flex justify-content-between pb-2">
                <div>
                  <strong> Driving Test Package</strong>
                </div>
                <div className="bookNow__drivingPrice d-flex align-items-center">
                  <strong>${testPackages[0]?.price}</strong>
                  {/* <button className="delete-icon" onClick={() => handleSettestPackage(!handleTestPackage)}>X</button> */}
                </div>
              </div>
            ) : (
              ""
            )}
            
          </div>
        </div>
      </div>
    </div>
  );
}

export default Step4;
