const newformatDate = (format) => (dateString) => {
    if (!dateString) return ""; // Handle empty or undefined values
  
    const [year, month, day] = dateString.split("-");
    if (format === "DD/MM/YYYY") {
      return `${day}/${month}/${year}`;
    } else if (format === "MM/DD/YYYY") {
      return `${month}/${day}/${year}`;
    }
    return dateString; // Default: Return original format if format not recognized
  };
  
  // Usage:
  const toDDMMYYYY = newformatDate("DD/MM/YYYY");
  
//  console.log(toDDMMYYYY("2025-02-26")); // Output: 26/02/2025
//  console.log(toDDMMYYYY("1999-12-31")); // Output: 31/12/1999
  

  export default toDDMMYYYY;