import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { apiGet, apiPost } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import { useAuth } from "../context/AuthContext";
import Step1 from "./Form/Step1";
import Step2 from "./Form/Step2";
import Step3 from "./Form/Step3";
import Step4 from "./Form/Step4";

function BookNow() {
  const location = useLocation();
  const {  filtersuburb, filterstate, filterpostcode, filterSuburbID, filterStateID } = useAuth();
  //console.log('filterStateID', filterStateID)
  const [instructorID, setInstructorID] = useState(location.state?.id || null);
 // console.log(instructorID)
  const [state, setState] = useState({
    step: 1,
    lessonHours: {},
    pricingData: {},
    testPackages: [],
    selectedPackages: [], // Added state to store selected packages
    relationsData: [],
    statesData: {},
  });

  // For step 1
  const [getHours, setGetHours] = useState(1);
  const [drivingLessonCost, setDrivingLessonCost] = useState(); //-----------------------------------------------------------------

  useEffect(() => {
   // setDrivingLessonCost(state.lessonHours[0]?.price);
  }, []);

  useEffect(() => {
    fetchData();
    // console.log(getHours)
  }, [getHours]);

  // const drivingLessonCost =  getHours * parseFloat(state.lessonHours[0]?.price.replace("$", "").trim()) || 75;

  const handleCheckBoxDrivingLesson = (e, name) => {
    // console.log(e.target.value, name, drivingLessonCost);
    return e.target.value;
  };

  const [drivingLesson, setDrivingLesson] = useState(false);
  const [testPackage, setTestPackage] = useState(false);
  const realTestPackageCost = testPackage ? +state.testPackages[0]?.price : 0;
  const packageSum =
    (drivingLesson ? +drivingLessonCost : 0) + realTestPackageCost;

  const fetchData = async () => {
    try {
      // dispatch(startLoading());
      const [
        lessonHoursRes,
        pricingRes,
        testPackageRes,
        relationsRes,
        statesRes,
      ] = await Promise.all([
        apiGet(API_ENDPOINTS.PRICES),
        apiGet(API_ENDPOINTS.PRICING_STRUCTURE),
        apiGet(API_ENDPOINTS.TEST_PACKAGE),
        apiGet(API_ENDPOINTS.RELATIONS),
        apiGet(API_ENDPOINTS.STATES),
      ]);

      // console.log(relationsRes);
      // Handle lesson hours response
      if (lessonHoursRes.success && lessonHoursRes.data?.hours_list) {
        // console.log(lessonHoursRes);
        setState((prevState) => ({
          ...prevState,
          lessonHours: lessonHoursRes.data.hours_list,
        }));
       
      } else {
        console.error("Unexpected lesson hours response:", lessonHoursRes);
      }

      // Handle pricing data response
      if (pricingRes.success && pricingRes.data?.hours_list) {
        // console.log("pricingRes is" + pricingRes);
        setState((prevState) => ({
          ...prevState,
          pricingData: pricingRes.data.hours_list,
        }));
      } else {
        console.error("Unexpected pricing response:", pricingRes);
      }

      //  Handle test package response
      if (testPackageRes?.data?.[0]) {
        setState((prevState) => ({
          ...prevState,
          testPackages: testPackageRes.data,
        }));
      } else {
        console.error("Unexpected test package response:", testPackageRes);
      }

      //  Handle relations response
      if (relationsRes?.data) {
        setState((prevState) => ({
          ...prevState,
          relationsData: relationsRes.data,
        }));
      } else {
        console.error("Unexpected test package response:", relationsRes);
      }
      // handle states response
      if (statesRes?.data) {
        setState((prevState) => ({
          ...prevState,
          statesData: statesRes.data,
        }));
      } else {
        console.error("Unexpected test package response:", statesRes);
      }
    } catch (error) {
      // setError(error.message); // Set the error message to state
      console.error(error); // Log error for debugging
    } finally {
      // dispatch(finishLoading());
    }
  };

  // Book now features
  // Step 3
  const [localData, setLocalData] = useState({
    myself: "",//true
    street: "",
    suburb_id: "",
    state_id: filterStateID || "",
    first_name: "",
    last_name: "",
    email: "",
    phone_no: "",
    dob: "",
    description: "",
    password: "",
    agreeToMarketing: false,
    agreeToTerms: false,
    state_slug:'',// new requirement for address in autocomplete
    suburb:'',// new requirement for address in autocomplete
    state_name:'',// new requirement for address in autocomplete
    postcode :'',// new requirement for address in autocomplete
    
    // learner's details
    learner_first_name: "",
    learner_last_name: "",
    learner_phone_no: "",
    relation_id: "",

    // payment form
    payment_status: "",
    billing_name: "",
    billing_address: "",
    billing_suburb_id: "",
    billing_state_id: "",
    //generalBillStreet:'',
    generalBillSuburb:'',
    generalBillState:'',
//generalBillStreet
    billing_suburb :'',
    billing_state_slug :'',
    billing_state_name:'',
    billing_postcode:'',
  });
 // console.log(localData, "localData");

  // Next step
  const nextStep = () => {
    setState((prevState) => ({ ...prevState, step: state.step + 1 }));
  };

  // location fetch
  const [inputValue, setInputValue] = useState(filtersuburb || '');
  const [suburbID, setSuburbID]= useState(filterSuburbID  || '');  //get suburb id from context Api 
  const [suggestions, setSuggestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [isSuggestionSelected, setIsSuggestionSelected] = useState(false);
  const [noResults, setNoResults] = useState(false);
  const [suburbPostCode, setSuburbPostCode]= useState();
//console.log('filterSuburbID', filterSuburbID)
  const handleInputChange = (e) => {
    const value = e.target.value;
    setInputValue(value);
     // setIsValidInput(true);
      setIsSuggestionSelected(false);
 //   console.log('input change')

  };

  const fetchSuggestions = async (query) => {
    setLoading(true);
    setNoResults(false);

    try {
      const response = await apiPost(API_ENDPOINTS.FIND_SUBURBS, {
        filter: query,
      });

      if (response.success && response.data) {
        const filteredSuggestions = response.data.filter((item) =>
          [item.suburb, item.stateCode, item.postcode.toString()].some(
            (field) => field.toLowerCase().includes(query.toLowerCase())
          )
        );

        setNoResults(filteredSuggestions.length === 0);
        setSuggestions(filteredSuggestions);
      } else {
        setSuggestions([]);
        setNoResults(true);
      }
    } catch (error) {
      console.error("Error fetching suggestions:", error);
      setSuggestions([]);
      setNoResults(true);
    } finally {
      setLoading(false);
    }
  };
  const handleSuggestionClick = (suggestion) => {
    setInputValue(
      `${suggestion.suburb}, ${suggestion.stateCode} (${suggestion.postcode})`
    );
    setSuburbID(suggestion.id)
    setIsSuggestionSelected(true);
    setSuburbPostCode(suggestion.postcode)
    setSuggestions([]);
  };

  useEffect(()=>{
    if(filtersuburb !== 'undefined'){
      setSuggestions([]);
      setIsSuggestionSelected(true);
    }
  },[])


  useEffect(() => {
    if (!isSuggestionSelected && inputValue.trim() !== "") {
      const delayDebounceFn = setTimeout(() => {
        fetchSuggestions(inputValue);
       // console.log('debounce called')
      }, 500);

      return () => clearTimeout(delayDebounceFn);
    }
  }, [inputValue, isSuggestionSelected]);

//

const[lessonBookingData, setLessonBookingData]= useState([])// store lesson booking info from step 2
//
  return (
    <>
      {/* {console.log(state)} */}
      <div className="stepper shadow-lg">
        <Container>
          <Row>
            <Col lg={12}>
              <div className="stepper__wrp">
                {[1, 2, 3, 4].map((stepIndex) => (
                  <div
                    key={stepIndex}
                    className={`stepper__step d-flex gap-3 align-items-center justify-content-center ${
                      state.step === stepIndex
                        ? "active"
                        : state.step > stepIndex
                        ? "step-done"
                        : ""
                    }`}
                  >
                    <div className="stepper__stepCount">
                      <h2>{stepIndex}</h2>
                    </div>
                    <div>
                      <p>Step {stepIndex}</p>
                      <p>
                        {stepIndex === 1
                          ? "Confirm Lesson"
                          : stepIndex === 2
                          ? "Book Lesson"
                          : stepIndex === 3
                          ? "Your Details"
                          : "Make Payment"}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </Col>
          </Row>
        </Container>
      </div>

      <div className="bg-grey bookNow">
        <Container>
          {state.step === 1 && (
            <Step1
              // formData={formData}
              // handleChange={handleChange}
              // onContinue={onContinue}
              // updateSelectedPackages={updateSelectedPackages} // Pass callback
              lessonHours={state.lessonHours}
              pricingData={state.pricingData}
              testPackages={state.testPackages}
              handleCheckBoxChange={handleCheckBoxDrivingLesson}
              handleSetHours={setGetHours}
              handleGetHours={getHours}
              handleDrivingLessonCost={drivingLessonCost}
              handleSetdrivingLessonCost={setDrivingLessonCost} // passing this to change the price
              handleDrivingLesson={drivingLesson}
              handleSetDrivingLesson={setDrivingLesson}
              handleTestPackage={testPackage}
              handleSettestPackage={setTestPackage}
              handlePackageSum={packageSum}
              nextStep={nextStep}
            />
          )}
          {state.step === 2 && (
            <Step2
              selectedPackages={state.selectedPackages} // Pass data to Step 2 if needed
              nextStep={nextStep}
              trainerID={instructorID}
              handleGetHours={getHours}
              setLessonBookingData={setLessonBookingData}
              handleDrivingLesson={drivingLesson}
              handleTestPackage={testPackage}
              testPackages={state.testPackages}
            />
          )}
          {state.step === 3 && (
            <Step3
              handleLocalData={localData}
              handleSetLocalData={setLocalData}
              handleDrivingLesson={drivingLesson}
              handleDrivingLessonCost={drivingLessonCost}
              handleTestPackage={testPackage}
              handlePackageSum={packageSum}
              sendTestPackageCost={realTestPackageCost}
              handleGetHours={getHours}
              testPackages={state.testPackages}
              handleStatesData={state.statesData}
              // location
              inputValue={inputValue}
              suburbID={suburbID}
              trainerID={instructorID}
              lessonBookingData={lessonBookingData}
              suburbPostCode={suburbPostCode}
             
              nextStep={nextStep}
              handleRelationsData={state.relationsData}
              // location
              suggestions={suggestions}
              loading={loading}
              noResults={noResults}
              handleInputChange={handleInputChange}
              handleSuggestionClick={handleSuggestionClick}
             
              
              
            />
          )}
          {state.step === 4 && (
            <Step4
              handleLocalData={localData}
              handleSetLocalData={setLocalData}
              handleDrivingLesson={drivingLesson}
              handleDrivingLessonCost={drivingLessonCost}
              handleTestPackage={testPackage}
              handlePackageSum={packageSum}
              sendTestPackageCost={realTestPackageCost}
              handleGetHours={getHours}
              testPackages={state.testPackages}
              handleStatesData={state.statesData}
              inputValue={suburbID}// sending the suburd.id
              trainerID={instructorID}
              lessonBookingData={lessonBookingData}
              suburbPostCode={suburbPostCode}
            />
          )}
        </Container>
      </div>
    </>
  );
}

export default BookNow;
