import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import logo from "../../assets/images/logo.svg";
import adminIco from "../../assets/images/admin.jpeg";
import notiIco from "../../assets/images/noti-icon.svg";
import { useAuth } from "../../context/AuthContext";

function Header({ navMenuData }) {
  const navigate = useNavigate();
  const { setRole, userData, newProfileImg, newProfileName,setNewProfileImg, setNewProfileName, setToken } = useAuth();
  // console.log(userData)
  const [menuActive, setMenuActive] = useState(false);

  const toggleMenu = () => {
    setMenuActive(!menuActive);
  };

  const closeMenu = () => {
    setMenuActive(false);
  };

  const handleLogout = () => {
    // Clear the localStorage
    sessionStorage.removeItem("authToken");
    sessionStorage.removeItem("userDetails");
    sessionStorage.removeItem("role");
    sessionStorage.clear();//setNewProfileImg setNewProfileName
    setToken("");
    setNewProfileImg(''); setNewProfileName('')
    setRole("");

    // Redirect to login page
    navigate("/learner-login");
  };

  return (
    <div className={`trainerHeader ${menuActive ? "menu-active" : ""}`}>
      <div className="trainerHeader__wrp">
        <div className="trainerHeader__logo">
          <Link to="/" onClick={closeMenu}>
            <img src={logo} alt="Logo" />
          </Link>
        </div>

        <ul className={`trainerHeader__menu ${menuActive ? "active" : ""}`}>
          {/* <li className="trainerHeader__notification">
            <img src={notiIco} alt="" />2 Notifications
          </li> */}
          <li className="trainerHeader__profile">
            <img
              src={newProfileImg=== '' ? (userData.profile_picture ? userData.profile_picture : adminIco): newProfileImg }
              //newProfileImg === 'null' ? (userData.profile_image ? userData.profile_image : adminIco): newProfileImg
              
              alt=""
            />
            Welcome,{" "}
            <strong>
              {newProfileName ? newProfileName : (userData.first_name ? userData.first_name : "User")}
            </strong>
            <div className="trainerHeader__profileListing">
              <ul className="">
                <li>
                  <Link to="/learner/personal-details"> View Profile</Link>
                </li>
                <li onClick={handleLogout}>
                  {" "}
                  <Link>Log out</Link>{" "}
                </li>
              </ul>
            </div>
          </li>
        </ul>
        {/* Hemburger icon - visible only on mobile screens */}
        <div className="trainerHeader__hemburger" onClick={toggleMenu}>
          {menuActive ? (
            <span className="close-icon">✕</span>
          ) : (
            <span className="hemburger-icon">☰</span>
          )}
        </div>
      </div>
    </div>
  );
}

export default Header;
