import axios from "axios";

// Create an Axios instance
const axiosInstance = axios.create({
  baseURL:
    process.env.REACT_APP_API_BASE_URL ||
    "https://ready-set-driving-school.pwd.net.au",
  timeout: 10000,
  headers: {
    "Content-Type": "application/json",
  },
});

// Interceptor for requests (e.g., adding authorization tokens)
axiosInstance.interceptors.request.use(
  (config) => {
    const token = sessionStorage.getItem("authToken");
   // console.log(token)
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// Interceptor for responses (e.g., error handling)
axiosInstance.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
    //  console.log("Unauthorized! Redirecting to login...");
    }
    return Promise.reject(error.response?.data?.data?.error || error?.response?.data?.message || "An error occurred");
  }
);

///////////////////Helper functions for API methods/////////////////////////////
// ///////////////////////////////////////////////////////////////////formData
// Create a second Axios instance for FormData requests
const axiosFormDataInstance = axios.create({
  baseURL:
    process.env.REACT_APP_API_BASE_URL ||
    "https://ready-set-driving-school.pwd.net.au",
  timeout: 10000,
  headers: {
    "Content-Type": "multipart/form-data",
  },
});
axiosFormDataInstance.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
     // console.log("Unauthorized! Redirecting to login...");
    }
    return Promise.reject(error.response?.data?.data?.error || error?.response?.data?.message || "An error occurred");
  }
);

// Helper function for POST requests with FormData
export const apiPostFormData = async (url, formData) => {
  try {
    const response = await axiosFormDataInstance.post(url, formData);
    return response.data;
  } catch (error) {
    throw error;
  }
};
// /////////////////////////////////////////////////////////////////////////formData
//For API GET METHOD
export const apiGet = async (url, params = {}) => {
  try {
    const response = await axiosInstance.get(url, { params });
    return response.data;
  } catch (error) {
    throw error;
  }
};

//For API POST METHOD
export const apiPost = async (url, data = {}, config = {}) => {
  try {
    const response = await axiosInstance.post(url, data, {
      ...config, // Spread the custom config
    });
    
    
    return response.data;
  } catch (error) {
   
    
    return error;
  }
};

