//For API PATCH METHOD
export const apiPatch = async (url, data = {}) => {
  try {
    const response = await axiosInstance.patch(url, data);
    return response.data;
  } catch (error) {
    throw error;
  }
};

//For API PUT METHOD
export const apiPut = async (url, data = {}) => {
  try {
    const response = await axiosInstance.put(url, data);
    return response.data;
  } catch (error) {
    throw error;
  }
};

//For API DELETE METHOD
export const apiDelete = async (url) => {
  try {
    const response = await axiosInstance.delete(url);
    return response.data;
  } catch (error) {
    throw error;
  }
};

// Create an Axios instance with Authorization header
export const axiosWithAuth = (token) => {
  return axios.create({
    baseURL: process.env.REACT_APP_API_BASE_URL, // Base URL for your API
    headers: {
      Authorization: `Bearer ${token}`, // Automatically include the token
    },
  });
};


export { axiosFormDataInstance };
export default axiosInstance;
