import React, { createContext, useContext, useState, useEffect } from "react";
const AuthContext = createContext();
export const AuthProvider = ({ children }) => {
  const [role, setRole] = useState(undefined); // Default role is undefined
  const [userData, setUserData] = useState({}); // Default userData
  const [token, setToken] = useState(""); // Default token
  const [loading, setLoading] = useState(true); // Track loading state
  const [instructor, setInstructor] = useState({}) //storing instructor detail in case of learner login
  const [bookNow, setBookNow] = useState({})// storing the book now response at booking time from general website
  const [newProfileImg, setNewProfileImg] = useState('');//storing the update image from profile
  const [newProfileName, setNewProfileName] = useState('');//storing the updated name from profile
  // Other state variables
  const [filtersuburb, setFilterSuburb] = useState();
  const [filterstate, setFilterState] = useState();
  const [filterpostcode, setFilterPostcode] = useState();
  const [filterSuburbID, setFilterSuburbID] = useState();
  const [filterStateID, setFilterStateID] = useState();
  const [selectSearchValue, setSelectSearchValue] = useState(false);
  //learner related data
  const [bookWithNewTrainerID, setBookWithNewTrainerID] = useState('');
  const [addressSuburb, setAddressSuburb]= useState([]);
  const [learnerCreditHours, setLearnerCreditHours] = useState(0); //storing the learner credit hours
  const [learnerCreditAmount, setLearnerCreditAmount] = useState(0);//storing the learner credit amount
  const [learnerBookData, setLearnerBookData] = useState({
    //bookWithNewTrainerID:'',
    bookWithTrainerID:'',
    bookTrainerInfo:'',
    lessonBookingData:[],
    totalBookHours:'',
    perHourPriceList:[],
    allStaeList:[],
    totalBookHourPrice:0,
    totalTestPackagePrice:0,
    preferred_pickup_address: addressSuburb.length > 0  ? `${addressSuburb[0]?.long_name} ${addressSuburb[1]?.long_name} ${addressSuburb[2]?.short_name} ${addressSuburb[3]?.long_name} ${addressSuburb[4]?.long_name}` : "",
   // preferred_pickup_address: `${addressSuburb[0]?.long_name}`,
    suburb: addressSuburb[addressSuburb.length - 5]?.long_name || '',// full name
    state:  addressSuburb[addressSuburb.length - 3]?.long_name || "",
    state_name:addressSuburb[addressSuburb.length - 3]?.long_name || "",//
    state_slug: addressSuburb[addressSuburb.length - 3]?.short_name || "",//
    postcode: addressSuburb[addressSuburb.length - 1]?.long_name || "",//
    optional_note_instructor: '',
    //billing details
    billing_name: "",
    billing_address: "",
    billing_suburb :'',
    billing_state_slug :'',
    billing_state_name:'',
    billing_postcode:'',
      })
  
// Update learnerBookData whenever addressSuburb changes
useEffect(() => {//this useffect is done for reinitialize the address value to respective keys when address changes
  setLearnerBookData((prevData) => ({
    ...prevData,
    preferred_pickup_address:  addressSuburb.length > 0  ? `${addressSuburb[0]?.long_name} ${addressSuburb[1]?.long_name} ${addressSuburb[2]?.short_name} ${addressSuburb[3]?.long_name} ${addressSuburb[4]?.long_name}` : "",
    suburb: addressSuburb[addressSuburb.length - 5]?.long_name || '',
    state: addressSuburb[addressSuburb.length - 3]?.long_name || '',
    state_name: addressSuburb[addressSuburb.length - 3]?.long_name || '',
    state_slug: addressSuburb[addressSuburb.length - 3]?.short_name || '',
    postcode: addressSuburb[addressSuburb.length - 1]?.long_name || '',
  }));
}, [addressSuburb]);


  useEffect(() => {
  // console.log(learnerBookData)
    const fetchAuthDetails = async () => {
      try {
        setLoading(true);

        // Fetch data from sessionStorage
        const typeRole = sessionStorage.getItem("role");
        const userDetail = sessionStorage.getItem("userDetails");
        const userToken = sessionStorage.getItem("authToken");
        const instructorDetail = sessionStorage.getItem("instructor");
        const profileImage = sessionStorage.getItem("profileImg");
        const profileName = sessionStorage.getItem("profileName");
       // const newSelectedTrainerId= sessionStorage.getItem("trainerNewId");

        if (typeRole) {
          setRole(typeRole);
        } else {    
          //console.warn("No role found."); 
          }
          if(profileImage){
            setNewProfileImg(profileImage)
          }else{

          }
          if(profileName){
            setNewProfileName(profileName)
          }else{
            
          }

        if (userDetail) {
          setUserData(JSON.parse(userDetail));
        } else {   
         // console.warn("No user details found."); 
         }

        if (userToken) {
          setToken(userToken);
        } else {  console.warn("No token found.");   }
        if(instructorDetail){
          setInstructor(instructorDetail)
        } else{  console.warn("No instructor detail found.");}
        // if(newSelectedTrainerId){
        //   setLearnerBookData((prevData) => ({
        //     ...prevData,
        //     bookWithTrainerID:newSelectedTrainerId,})
        //   )
        // }else{  console.warn("No instructor detail found.");}
      } catch (error) {
       // console.error("Error fetching auth details:", error);
      } finally {  setLoading(false); // Stop loading when data fetch completes
      }
    };

    fetchAuthDetails();
  }, []); // Run only on mount

  return (
    <AuthContext.Provider
      value={{
        role,  setRole,  token, setToken, userData,  setUserData, loading, instructor, setInstructor, filtersuburb, setFilterSuburb, filterstate, setFilterState,
        filterpostcode, setFilterPostcode, filterSuburbID, setFilterSuburbID,  filterStateID,  setFilterStateID, selectSearchValue, setSelectSearchValue,
        learnerBookData, setLearnerBookData, addressSuburb, setAddressSuburb, setBookWithNewTrainerID, bookWithNewTrainerID,
        bookNow, setBookNow,
        newProfileImg, setNewProfileImg, newProfileName, setNewProfileName,
        setLearnerCreditHours, learnerCreditHours,
        learnerCreditAmount, setLearnerCreditAmount
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
