import React, { useState } from "react";
import { Link } from "react-router-dom";
import dashboardIco from "../../assets/images/dashboardIco.svg";
import calendarIco from "../../assets/images/calendarIco.svg";
import learnersIco from "../../assets/images/learnersIco.svg";
import paymentsIco from "../../assets/images/paymentsIco.svg";
import reportsIco from "../../assets/images/reportsIco.svg";
import accountSettingsIco from "../../assets/images/accountSettingsIco.svg";
import supportIco from "../../assets/images/supportIco.svg";

export default function VerticalMenu() {
  const [isActive, setIsActive] = useState(false);
  // Toggle the active state on click
  const toggleMenu = () => {
    setIsActive(!isActive);
  };
  const hideVerticalMenu=()=>{
    setTimeout(()=>{
      setIsActive(!isActive);
    },500)
  }
  return (
    <>
      <div className={`verticalMenu ${isActive ? 'active' : ''}`}>
        <div className="verticalMenu__hemburger" onClick={toggleMenu}><span className="hemburger-icon">☰</span></div>
        <ul onClick={hideVerticalMenu}>
          <li>
            <Link to="/instructor/dashboard" onClick={toggleMenu}>
              <img src={dashboardIco} alt="" /> Dashboard
            </Link>
          </li>
          {/* <li>
            <Link to="/instructor/calendar">
              <img src={calendarIco} alt="" /> Calendar
            </Link>
          </li> */}
          <li>
            <Link to="/instructor/learner-list" onClick={toggleMenu}>
              <img src={learnersIco} alt="" />
              Learners
            </Link>
          </li>
          <li>
            <Link to="/instructor/payments" onClick={toggleMenu}>
              <img src={paymentsIco} alt="" />
              Payments
            </Link>
          </li>
          <li>
            <Link to="/instructor/reports" onClick={toggleMenu}>
              <img src={reportsIco} alt="" />
              Reports
            </Link>
          </li>
          <li>
            <Link to="/instructor/account-setting" onClick={toggleMenu}>
              <img src={accountSettingsIco} alt="" />
              Account Settings
            </Link>
          </li>
          {/* <li>
            <Link to="/instructor/dashboard">
              <img src={supportIco} alt="" />
              Support
            </Link>
          </li> */}
        </ul>
      </div>
    </>
  );
}
