import React, { useEffect } from "react";
import bookNow__drivingLessonsListingIco from "../../assets/images/bookNow__drivingLessonsListingIco.png";
import OtherCardInfo from "../PDP/OtherCardInfo";
//import { isEmpty } from "../../helpers/index";

function Step1({
  lessonHours,
  pricingData,
  testPackages,
  handleCheckBoxChange,
  handleSetHours,
  // handleGetHours,
  handleDrivingLessonCost,
  handleSetdrivingLessonCost,
  handleDrivingLesson,
  handleSetDrivingLesson,
  handleTestPackage,
  handleSettestPackage,
  handlePackageSum,
  nextStep,
}) {
  const changeSelect = (e) => {
    //  console.log(e.target.value)
    handleSetHours(e.target.value);
    handleSetdrivingLessonCost(e.target.selectedOptions[0].dataset.price || lessonHours[0]?.price);
   // const selectedPrice = e.target.selectedOptions[0].dataset.price; // Access the price using data-price
   // console.log(selectedPrice);
  };
  //  const drivingLessonCost = handleGetHours * parseFloat(lessonHours[0]?.price.replace('$', '').trim()) || 0;

  useEffect(() => {
    handleSetdrivingLessonCost(lessonHours[0]?.price)
  }, [lessonHours[0]?.price]);

  useEffect(() => {
   
  }, [lessonHours, testPackages, pricingData, handleDrivingLesson]);
  // console.log("lessonHours" + lessonHours);
  // console.log("pricing" + pricingData);
  return (
    <div className="bookNow__stepWrap d-flex">
      <div className="bookNow__stepWrapLeft">
        <h2> Select Lesson Type </h2>
        {/* Driving Lessons Option */}
        <div className="bookNow__drivingLessons driving-lesson">
        <label style={{width:'100%'}}>
          <div className="bookNow__drivingLessonsHead d-flex align-items-center">
            
            <input
              type="checkbox"
              name="drivingLessonsHead"
              value="Driving Lessons"
              checked={handleDrivingLesson}
              onChange={(e) => {
                handleCheckBoxChange(e);
                handleSetDrivingLesson(!handleDrivingLesson);
              }}
            />
            

            <div className="CheckboxHighlight"> </div>
            <h3> Driving Lessons </h3>
            
          </div>
          </label>
          {/* Driving Lessons Details */}
          <div className="bookNow__drivingLessonsInner bg-white d-flex justify-content-between">
            <div className="bookNow__timeHours">
              <select
                value={lessonHours?.price}
                onChange={(e) => changeSelect(e)}
              >
                {lessonHours.length > 0
                  ? lessonHours.map((option, index) => (
                      <option key={index} value={option.hours} data-price={option.price}>
                        {option.hours}
                      </option>
                    ))
                  : ""}
              </select>
              &nbsp;&nbsp;hr(s) &nbsp;&nbsp;&nbsp;
              <span>{lessonHours[0]?.price}/hr</span>
            </div>

            <div className="bookNow__drivingPrice">
              <h3>${handleDrivingLessonCost } total</h3>
            </div>
          </div>
          <div className="pdpWrapper__table bg-grey">
            <div className="pdpWrapper__tableHead">
              <h5> Driving Lesson Pricing Structure </h5>
              <p> How many lessons do I need </p>
            </div>
            <div className="pdpWrapper__tableMid">
              {pricingData.length > 0 ? (
                pricingData.map((pricing, index) => (
                  <div
                    key={index}
                    className="pdpWrapper__tableRow d-flex justify-content-between"
                  >
                    <p>
                      <strong> {pricing?.hours} </strong> hours
                    </p>
                    <div className="d-flex">
                      <p>
                        <strong> {pricing?.price} </strong> / hr
                      </p>
                      <div className="btn btn-teal btn-sm d-none"> 10 % Saving </div>
                    </div>
                  </div>
                ))
              ) : (
                <p> No pricing data available. </p>
              )}
            </div>
            <div className="pdpWrapper__tableBottom">
              <div className="pdpWrapper__tableBottomDisclaimer">
                <p> * This driver offers 1 - 2 hour long lessons </p>
              </div>
            </div>
          </div>
        </div>
        {/* Driving Test Package Option */}
        <div className="bookNow__drivingLessons driving-test-package">
        <label style={{width:'100%'}}>
          <div className="bookNow__drivingLessonsHead d-flex align-items-center bg-grey">
            <input
              type="checkbox"
              name="drivingTestPackage"
              value="Driving Test Package"
              checked={handleTestPackage}
              onChange={(e) => {
                handleSettestPackage(!handleTestPackage);
              }}
            />
            <div className="CheckboxHighlight"> </div>
            <h3> Driving Test Package </h3>
          </div>
          </label>
          {/* Driving Test Package Details */}
          <div className="bookNow__drivingLessonsInner bg-white d-flex justify-content-between">
            <div className="bookNow__drivingLessonsListing d-flex">
              <img
                src={bookNow__drivingLessonsListingIco}
                alt="Driving Lessons Listing Icon"
              />
              <div
                className=""
                dangerouslySetInnerHTML={{
                  __html: testPackages[0]?.listing || "Loading...",
                }}
              />
              
            </div>
            <div className="bookNow__drivingPrice">
              <h3> $ {testPackages[0]?.price} </h3>
            </div>
          </div>
        </div>
      </div>
      {/* Right Side: Selected Package Summary */}
      <div className="bookNow__stepWrapRight">
        <div className="bookNow__drivingLessons">
          <div className="bookNow__drivingLessonsHead text-center text-black bg-light-grey">
            <h4> Your Selected Package </h4>
          </div>
          <div className="bookNow__subTotal bg-white">
            {handleDrivingLesson ? (
              <div className="bookNow__subTotalrow d-flex justify-content-between pb-2">
                <div>
                  <strong>Driving Lessons</strong>
                </div>
                <div className="bookNow__drivingPrice d-flex align-items-center">
                  <strong>${handleDrivingLessonCost}</strong>
                  <button
                    onClick={() => handleSetDrivingLesson(!handleDrivingLesson)}
                    className="delete-icon"
                  >
                    X
                  </button>
                </div>
              </div>
            ) : (
              ""
            )}
            {handleTestPackage ? (
              <div className="bookNow__subTotalrow d-flex justify-content-between pb-2">
                <div>
                  <strong> Driving Test Package</strong>
                </div>
                <div className="bookNow__drivingPrice d-flex align-items-center">
                  <strong>${testPackages[0]?.price}</strong>
                  <button className="delete-icon" onClick={() => handleSettestPackage(!handleTestPackage)}>X</button>
                </div>
              </div>
            ) : (
              ""
            )}

            <div className="bookNow__subTotalrow d-flex justify-content-between">
              <strong>Total</strong>
              <div className="bookNow__drivingPrice">
                {/* <strong>${subtotal.toFixed(2)}</strong> */}
                <strong>${handlePackageSum + ".00" || "0.00 "} </strong>
              </div>
            </div>
            <div className="bookNow__continueBooking">
              <button
                type="button"
                className="btn btn-blue"
                onClick={nextStep}
                disabled={!handleDrivingLesson && !handleTestPackage }
              >
                CONTINUE BOOKING &#62;
              </button>
            </div>
          </div>
        </div>
        <div className="d-flex items-center gap-2 px-3">
          <svg
            width="20"
            height="20"
            viewBox="0 0 20 20"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 7.64012C10.1894 7.64012 10.3711 7.7133 10.5051 7.84355C10.639 7.97381 10.7143 8.15047 10.7143 8.33468V14.5857C10.7143 14.7699 10.639 14.9466 10.5051 15.0768C10.3711 15.2071 10.1894 15.2802 10 15.2802C9.81056 15.2802 9.62888 15.2071 9.49492 15.0768C9.36097 14.9466 9.28571 14.7699 9.28571 14.5857V8.33468C9.28571 8.15047 9.36097 7.97381 9.49492 7.84355C9.62888 7.7133 9.81056 7.64012 10 7.64012ZM10 6.25101C10.2842 6.25101 10.5567 6.14124 10.7576 5.94586C10.9585 5.75048 11.0714 5.48549 11.0714 5.20917C11.0714 4.93286 10.9585 4.66787 10.7576 4.47249C10.5567 4.2771 10.2842 4.16734 10 4.16734C9.71584 4.16734 9.44332 4.2771 9.24239 4.47249C9.04145 4.66787 8.92857 4.93286 8.92857 5.20917C8.92857 5.48549 9.04145 5.75048 9.24239 5.94586C9.44332 6.14124 9.71584 6.25101 10 6.25101ZM0 9.72379C0 4.35348 4.47714 0 10 0C15.5229 0 20 4.35348 20 9.72379C20 15.0941 15.5229 19.4476 10 19.4476C4.47714 19.4476 0 15.0941 0 9.72379ZM10 1.38911C5.26643 1.38911 1.42857 5.12097 1.42857 9.72379C1.42857 14.3266 5.26643 18.0585 10 18.0585C14.7336 18.0585 18.5714 14.3266 18.5714 9.72379C18.5714 5.12097 14.7336 1.38911 10 1.38911Z"
              fill="black"
            />
          </svg>
          <u>More info about bookings</u>
        </div>
        <OtherCardInfo />
      </div>
    </div>
  );
}

export default Step1;
