import React, {useState, useEffect} from "react";
import { useLocation } from "react-router-dom";
import API_ENDPOINTS from "./utilities/apiEndPoints";
import { apiGet } from "./utilities/axiosInstance";

import ScrollToTop from "./Components/ScrollToTop";

import Topnav from "./Components/Topnav";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
// import InstructorHeader from "./InstructorHeader";
// import InstructorFooter from "./InstructorFooter";
import InstructorHeader from "./Trainer/Components/Header";
import VerticalMenu from "./Trainer/Components/VerticalMenu";
import InstructorFooter from "./Trainer/Components/Footer";

import LearnerVerticalMenu from './Learner-dashboard/Components/VerticalMenu';
import LearnerHeader from './Learner-dashboard/Components/Header';



const Layout = ({ children }) => {

  const location = useLocation();
  // State to store navigation menu data
  const [navMenuData, setNavMenuData] = useState([]);
  //const [isInstructorRoute, setIsInstructorRoute] = useState(false);
  //const [isLearnerRoute, setIsLearnerRoute] = useState(false);

  // Fetch menu data from API
  const fetchMenuData = async () => {
    try {
    //   dispatch(startLoading());
      const response = await apiGet(API_ENDPOINTS.NAV_MENU);
      if (response && response.data) {
        setNavMenuData(response.data);
      }
    //   dispatch(finishLoading());
    } catch (error) {
      console.error("Error fetching menu data:", error);
    //   dispatch(finishLoading());
    }
  };

  useEffect(() => {
    fetchMenuData(); // Fetch menu data on component mount
  }, []);


  // useEffect(()=>{
  //   if (!role) {
  //       setIsInstructorRoute(false);
  //       setIsLearnerRoute(false)
  //     }
  //     else{
  //       setIsInstructorRoute(location.pathname.startsWith("/instructor"));
  //       setIsLearnerRoute(location.pathname.startsWith("/learner/"))
  //     }

  // },[role])
  

  //const location = useLocation();

  // Check if the current route is part of the instructor section
  const isInstructorRoute = location.pathname.startsWith("/instructor/");
  const isLearnerRoute = location.pathname.startsWith("/learner/");



  return (
    <>
     

{isInstructorRoute ? (
  <>
    <InstructorHeader />
    <VerticalMenu />
  </>
) : isLearnerRoute ? (
  <>
    <LearnerHeader />
    <LearnerVerticalMenu />
  </>
) : (
  <>
    <ScrollToTop />
    <Topnav />
    <Header navMenuData={navMenuData} />
  </>
)}


      <main>{children}</main>
      {isInstructorRoute ? (<InstructorFooter />)
       
      : isLearnerRoute ? (<InstructorFooter />) : (<Footer navMenuData={navMenuData} /> )
      }
    </>
  );
};

export default Layout;
