import React, { useState, useEffect, Suspense, lazy } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { useSelector, useDispatch } from "react-redux";
import { ToastContainer } from "react-toastify";



// import Topnav from "./Components/Topnav";
// import Header from "./Components/Header";
// import Footer from "./Components/Footer";
// import ScrollToTop from "./Components/ScrollToTop";
import Loader from "./Components/Loader";
import Layout from "./Layout";
import PrivateRoute from "./PrivateRoute";

// import { apiGet } from "./utilities/axiosInstance"; // Import API utility
import {  startLoading,  finishLoading,} from "./redux/features/loader/loaderSlice";
// import API_ENDPOINTS from "./utilities/apiEndPoints";
import BookNow from "./Components/BookNow";
// import InstructorProfile from "./Components/Instructor/InstructorProfile";
import LearnerProfile from "./Components/Learner/LearnerProfile";
import MakeAnotherBooking from "./Learner-dashboard/Components/Dashboard/MakeAnotherBooking.jsx";

// Lazy load route components
const UserRoute = lazy(() => import("./Routing/UserRoute/UserRoute"));
const AboutUs = lazy(() => import("./Components/AboutUs"));
const Faq = lazy(() => import("./Components/Faq"));
const TestPackage = lazy(() => import("./Components/TestPackage"));
const Support = lazy(() => import("./Components/Support"));
// const Search = lazy(() => import("./Components/Instructor/PDP"));
const Search = lazy(() => import("./Components/Search"));
const InstructorPDP= lazy(() => import("./Components/Instructor/PDP"));
// const PrivacyPolicy = lazy(() => import("./Components/PrivacyPolicy"));
const LearnersTerms = lazy(() => import("./Components/LernersTerms"));
const InstructorTerm = lazy(() => import("./Components/InstructorTerm"));
const LearnerLogin = lazy(() => import("./Components/LearnerLogin"));
const InstructorLogin = lazy(() => import("./Components/InstructorLogin"));
const ForgetPassword = lazy(() => import("./Components/LearnerForgetPassword.jsx"));
const InstructorRegistration = lazy(() => import("./Components/Instructor/Registration"));
const Thankyou = lazy(() => import("./Components/Thankyou"));
const PaymentPolicy = lazy(() => import("./Components/PaymentPolicy"));
const SearchResult = lazy(() => import("./Components/SearchResult"));
const PDP = lazy(() => import("./Components/PDP/OtherCardInfo"));
const NotFound = lazy(() => import("./Components/NotFound"));
const Payment = lazy(() => import("./Components/Payment/Payment"));

const TermsAndConditions = lazy(() => import("./Components/terms-and-conditions"));
const Disclaimer = lazy(() => import("./Components/disclaimer"));
const PrivacyPolicy = lazy(() => import("./Components/PrivacyPolicy"));
const LearnerForgetPassword= lazy(() => import("./Components/LearnerForgetPassword")); 
const InstructorForgetPassword= lazy(() => import("./Components/InstructorForgetPassword")); 
const ResetPassword= lazy(() => import("./Components/ResetPassword")); 


// TermsAndConditions
// Disclaimer
// Privacy Policy

// trainer routes
const TrainerDashboard = lazy(()=> import("./Trainer/Components/dashboard/Dashboard.jsx"))
const BookingHisotry = lazy(()=>import ( "./Trainer/Components/dashboard/BookingHisotry.jsx"));
const PendingPraposal = lazy(()=>import ( "./Trainer/Components/dashboard/PendingPraposal.jsx"));
const AccountSetting= lazy(()=> import ( "./Trainer/Components/accountsetting/AccountSetting.jsx"));
const PersonalDetails= lazy(()=> import ( "./Trainer/Components/accountsetting/PersonalDetails.jsx"));
const MyBankingDetails= lazy(()=> import("./Trainer/Components/accountsetting/MyBankingDetails.jsx"));
const ServiceArea= lazy(()=> import ( "./Trainer/Components/accountsetting/ServiceAreaAvailability.jsx"));
const MyDocuments= lazy(()=>(import("./Trainer/Components/accountsetting/MyDocuments.jsx")));
const SubmissionHistory= lazy(()=>(import("./Trainer/Components/accountsetting/SubmissionHistory.jsx")));
const ProfileVehicleDetails= lazy(()=> import ( "./Trainer/Components/accountsetting/ProfileVehicleDetails"));
const UpcomingBooking= lazy(()=> import ( "./Trainer/Components/dashboard/UpcomingBooking.jsx"));
const LearnerList= lazy(()=> import ("./Trainer/Components/learnerlist/LearnerList.jsx"));
const LearnerListDetails= lazy(()=> import ("./Trainer/Components/learnerlist/LearnerDetails.jsx"));
const Payments= lazy(()=> import("./Trainer/Components/payments/Payment.jsx"));
const PaymentAuthorization= lazy(()=> import("./Trainer/Components/payments/PaymentAuthorisation.jsx"));
const Reports= lazy(()=> import("./Trainer/Components/reports/Reports.jsx"));
const Invoice= lazy(()=> import("./Trainer/Components/reports/invoice.jsx"));
const Calendar= lazy(()=> import("./Trainer/Components/calendar/Calendar.jsx"));
const NewBooking = lazy(()=> import("./Trainer/Components/calendar/NewBookingProposal.js"));

// Learner routes
const LearnerDashboard = lazy(()=> import("./Learner-dashboard/Components/Dashboard/Dashboard.jsx"));
const TrainerSearch = lazy(()=> import("./Learner-dashboard/Components/TrainerSearch.jsx"));
const TrainerSearchListing = lazy(()=> import("./Learner-dashboard/Components/TrainerSearchListing.jsx"));
const RescheduleBooking = lazy(()=> import("./Learner-dashboard/Reschedulebooking.jsx"));
const LearnerPersonalDetails = lazy(()=> import("./Learner-dashboard/PersonalDetails.jsx"));
const Preferences = lazy(()=> import("./Learner-dashboard/Preferences.jsx"));
const ChooseLocation = lazy(()=> import("./Learner-dashboard/Components/Dashboard/ChooseLocation.jsx"));
const BookLesson = lazy(()=> import("./Learner-dashboard/Components/Dashboard/BookLesson.jsx"));
const LearnerBookNow = lazy(()=> import("./Learner-dashboard/BookNow.jsx"));


function App() {
  const { loading } = useSelector((state) => state.loader);

  return (
    <>
      {/* <UserRoute/> */}
      {/* <AdminRoute/> */}
      <Router>
        <Suspense fallback={<Loader />}>
          {/* <ScrollToTop /> */}
          {/* <Topnav /> */}
          {/* Pass navMenuData to Header as a prop */}
          {/* <Header navMenuData={navMenuData} /> */}
          <Layout>
          <Routes>
            
              {/* general routes */}
            <Route path="/" element={<UserRoute />} />
            <Route path="/about-us" element={<AboutUs />} />
            <Route path="/faqs" element={<Faq />} />
            <Route path="/test-package" element={<TestPackage />} />
            <Route path="/support" element={<Support />} />
            <Route path="/search" element={<Search />} />
            {/* search profile dynamic routing */}
            <Route path="search/:id" element={<InstructorPDP />} /> 
             {/* search profile dynamic routing */}
            <Route path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route path="/learners-terms" element={<LearnersTerms />} />
            <Route path="/instructor-terms" element={<InstructorTerm />} />
            <Route path="/learner-login" element={<LearnerLogin />} />
            <Route path="/learner/learner-profile" element={<LearnerProfile/>}/>
            <Route path="/instructor-login" element={<InstructorLogin />} />
            {/* <Route path="/instructor/instructor-profile" element={<InstructorProfile/>}/> */}
            <Route path="/forget-password" element={<ForgetPassword />} />
            <Route path="/instructor-registration" element={<InstructorRegistration />} />
            <Route path="/thankyou" element={<Thankyou />} />
            <Route path="/payment-policy" element={<PaymentPolicy />} />
            <Route path="/search-result" element={<SearchResult />} />
            <Route path="/profile-details" element={<PDP />} />
            <Route path="/book-now" element={<BookNow />} />
            <Route path="*" element={<NotFound />} />
            <Route path="/payment" element={<Payment />} />
            <Route path="/terms-and-conditions" element={<TermsAndConditions />} />
            <Route path="/disclaimer" element={<Disclaimer />} />
            <Route path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route path="/learner-forget-password" element={<LearnerForgetPassword />} />
            <Route path="/instructor-forget-password" element={<InstructorForgetPassword />} />
            <Route path="/reset-password" element={<ResetPassword />} />
            
           
            {/* general routes */}

            {/* Instructor's protected routes */}
            {/* Dashboard */}
            <Route path="/instructor/dashboard" element={<PrivateRoute requiredRole="trainer"> <TrainerDashboard /> </PrivateRoute>  }/>
            <Route path="/instructor/dashboard/upcoming-booking" element={<PrivateRoute requiredRole="trainer"> <UpcomingBooking/> </PrivateRoute>} />
            <Route path="/instructor/dashboard/booking-history" element={<PrivateRoute requiredRole="trainer"> <BookingHisotry/> </PrivateRoute>}/>
            <Route path="/instructor/dashboard/pending-praposal" element={<PrivateRoute requiredRole="trainer"> <PendingPraposal/> </PrivateRoute>}/>
  
            {/* Account setting */}
            <Route path="/instructor/account-setting" element={<PrivateRoute requiredRole="trainer"> <AccountSetting/> </PrivateRoute>} />
            <Route path="/instructor/account-setting/personal-details" element={<PrivateRoute requiredRole="trainer"> <PersonalDetails/> </PrivateRoute>}/>
            <Route path="/instructor/account-setting/my-banking-details" element={<PrivateRoute requiredRole="trainer"> <MyBankingDetails/> </PrivateRoute>}/>
            <Route path="/instructor/account-setting/my-documents" element={<PrivateRoute requiredRole="trainer"> <MyDocuments/> </PrivateRoute>}/>
            <Route path="/instructor/account-setting/my-profile-and-vehicle-details" element={<PrivateRoute requiredRole="trainer"> <ProfileVehicleDetails/> </PrivateRoute>}/>
            <Route path="/instructor/account-setting/my-documents/submission-history" element={<PrivateRoute requiredRole="trainer"> <SubmissionHistory/> </PrivateRoute>}/>
            <Route path="/instructor/account-setting/service-area-availability" element={<PrivateRoute requiredRole="trainer" > <ServiceArea/> </PrivateRoute> } ></Route>

            {/* Learner list */}
            <Route path="/instructor/learner-list" element={<PrivateRoute requiredRole="trainer"> <LearnerList/> </PrivateRoute>  }/>
            <Route path="/instructor/learner-list-details/:id" element={<PrivateRoute requiredRole="trainer"> <LearnerListDetails/> </PrivateRoute>  }/>
            {/* Payments */}
            <Route path="/instructor/payments" element={<PrivateRoute requiredRole="trainer"> <Payments/> </PrivateRoute> } />
            <Route path="/instructor/payments/payment-authorisation" element={<PrivateRoute requiredRole="trainer"> <PaymentAuthorization/> </PrivateRoute> } />
            {/* Reports */}
            <Route path="/instructor/reports" element={<PrivateRoute requiredRole="trainer"> <Reports /> </PrivateRoute> } />
            <Route path="/instructor/reports/invoice" element={<PrivateRoute requiredRole="trainer"> <Invoice/> </PrivateRoute> } />
            {/* Calendar */}
            <Route path="/instructor/calendar" element={<PrivateRoute requiredRole="trainer"> <Calendar /> </PrivateRoute> } />
            <Route path="/instructor/NewBooking" element={<PrivateRoute requiredRole="trainer"> <NewBooking /> </PrivateRoute> } />




            {/* Learner's protected routes */}
            {/* Dashboard */}
            <Route path="/learner/dashboard" element={<PrivateRoute requiredRole="learner"> <LearnerDashboard /> </PrivateRoute>  }/>
            <Route path="/learner/make-another-booking" element={<PrivateRoute requiredRole="learner"> <MakeAnotherBooking /> </PrivateRoute>  }/>
            <Route path="/learner/search-trainer" element={<PrivateRoute requiredRole="learner"> <TrainerSearch /> </PrivateRoute>  }/>
            <Route path="/learner/search-trainer-listing" element={<PrivateRoute requiredRole="learner"> <TrainerSearchListing /> </PrivateRoute>  }/>
            
            <Route path="/learner/reschedule-booking" element={<PrivateRoute requiredRole="learner"> <RescheduleBooking /> </PrivateRoute>  }/>
            <Route path="/learner/personal-details" element={<PrivateRoute requiredRole="learner"> <LearnerPersonalDetails /> </PrivateRoute>  }/>
            <Route path="/learner/preferences" element={<PrivateRoute requiredRole="learner"> <Preferences /> </PrivateRoute>  }/>
            <Route path="/learner/choose-location" element={<PrivateRoute requiredRole="learner"> <ChooseLocation /> </PrivateRoute>  }/>
            <Route path="/learner/book-lesson" element={<PrivateRoute requiredRole="learner"> <BookLesson /> </PrivateRoute>  }/>
            <Route path="/learner/book-now" element={<PrivateRoute requiredRole="learner"> <LearnerBookNow /> </PrivateRoute>  }/>

            {/* <Route path="/learner/dashboard" element={<LearnerDashboard />} /> */}
            {/* protected routes */}
          
          </Routes>
          </Layout>
          {/* <Footer navMenuData={navMenuData} /> */}
        </Suspense>
      </Router>
      {loading && <Loader />}
      <ToastContainer />
    </>
  );
}

export default App;
