import React from "react";
import { Navigate } from "react-router-dom";
import { useAuth } from "./context/AuthContext";

const PrivateRoute = ({ children, requiredRole }) => {
  const { role, loading } = useAuth();

 // console.log("Inside PrivateRoute Component");
 // console.log("Role from useAuth:", role);
 // console.log("Loading from useAuth:", loading);

  if (loading) {
  //  console.log("Loading state: Showing loading indicator");
    return <div>Loading...</div>;
  }

  if (!role || typeof role !== "string" || role.trim() === "" || role === "undefined") {
  //  console.log("Redirecting to home because role is invalid or missing");
    return <Navigate to="/" replace />;
  }

  if (requiredRole && role !== requiredRole) {
   // console.log("Role mismatch detected. Redirecting...");
    const redirectPath = role === "trainer" ? "/learner-login" : role === "learner" ? "/instructor-login"  : "/";
    return <Navigate to={redirectPath} replace />;
  }

  //console.log("All conditions passed. Rendering children.");
  return children;
};

export default PrivateRoute;
